@import (less) "color.less";
.contenitor {
    display: table;
    border-collapse: separate;
    border-spacing: 0.312em;
    margin: 0 auto;
}

.letter {
    text-align: center;
}

.enabled {
    background-color: transparent;
    font-size: 0.937em;
    font-family: Arial, Regular;
    margin: 1px;
    height: 2em;
    width: 2em;
    color: #258900;
    border: 1px solid #258900;
    border-radius: 1.562em;
    display: table-cell;
    vertical-align: middle;
    cursor:pointer;
}

.enabled:hover {
    color: #FFFFFF;
    background-color: #258900;
}

.selected {
    background-color: transparent;
    font-size: 0.937em;
    font-family: Arial, Regular;
    margin: 1px;
    height: 2em;
    width: 2em;
    color: #FFFFFF;
    background-color: #258900;
    border-radius: 1.562em;
    display: table-cell;
    vertical-align: middle;
    cursor:pointer;
}

.disabled {
    background-color: transparent;
    font-size: 0.937em;
    font-family: Arial, Regular;
    margin: 1px;
    height: 2em;
    width: 2em;
    border-radius: 1.562em;
    display: table-cell;
    vertical-align: middle;
    pointer-events: none;
    opacity: 0.5;
    color: #CCCCCC;
    border: 1px solid #CCCCCC;
    cursor:none;
}



:host-context(.contrasto-on) {
    .enabled {
        background-color: transparent;
        color: @black;
        border: 1px solid @black;
    }
    
    .enabled:hover {
        color: #FFFFFF;
        background-color: @black;
    }
    
    .selected {
        color: #FFFFFF;
        background-color: @black;
    }
    
    .disabled {
        opacity: 0.5;
        color: @black;
        border: 1px solid @black;
    }
}